html {
  width: 100%;
  margin: 0;
  padding: 0;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  backface-visibility: hidden;
  /* overflow-x: hidden; */
}

body {
  position: relative;
  min-width: 320px;
  overflow-x: hidden;
  color: #000000;
  font-size: 16px;
  font-family: "OpenSansRegular", sans-serif;
  line-height: 1.42;
  opacity: 1;
}
body input,
body textarea {
  border: #666666 1px solid;
  outline: none;
}
body input:required:valid,
body textarea:required:valid {
  border-color: #008000;
}
body input:focus:required:invalid,
body textarea:focus:required:invalid {
  border-color: #ff0000;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}
.img-responsive.img-width {
  width: 100%;
}

